.secondaries {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column: span 7;
  grid-row: 2;
  gap: 30px;
}
.secondary {
  grid-column: span 3;
  display: flex;
  flex-direction: column;
  position: relative;
}
.secondary__header {
  margin: 0;
}
.secondary__button {
  background: none;
  border: none;
  cursor: pointer;
}
.secondary__value {
  margin: auto;
  font-size: 2.3rem;
}
.secondary-list {
  top: -15px;
  left: 0;
  width: calc(100% - 10px);
  height:100%;
  position: absolute;
  padding: 5px;
  background-color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 5px rgba(0,0,0,.3);
  max-height: 250px;
  overflow: auto;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.secondary-list__item:not(:first-child) {
  margin-top: 5px;
}
.secondary-list__button {
  width: 100%;
  padding: 3px;
  border:none;
  background: none;
  cursor: pointer;
}

.secondary-list__button:hover,
.secondary-list__button:focus {
  background-color: rgb(224, 224, 224);
}

@media screen and (max-width: 1265px) {
  .secondaries {
    grid-row: 2 / span 2;
  }
  .secondary {
    grid-column: span 6;
  }
}

@media screen and (max-width: 1230px) {
  
}

@media screen and (max-width: 950px) {
  .secondaries {
    grid-row: 3 / span 2;
  }
}

@media screen and (max-width: 680px) {
  .secondaries {
    grid-column: span 12;
  }
}

@media screen and (max-width: 420px) {
  .secondaries {
    grid-row: 3 / span 4;
    gap: 30px 0;
  }
  .secondary {
    grid-column: span 12;
  }
}